import { Pipe, PipeTransform } from '@angular/core';
import { DebitCardItem } from '@backbase/data-ang/arrangements';
import { PaymentCard } from '@backbase/ui-ang/payment-card';

/**
 * Transforms data to payment-card info
 */
@Pipe({ name: 'bbToPaymentCardData' })
export class ToPaymentCardDataPipe implements PipeTransform {
  transform(debitCard: DebitCardItem | undefined): PaymentCard {
    return {
      number: debitCard?.number || '',
      expirationDate: debitCard?.expiryDate ? new Date(debitCard.expiryDate) : (undefined as unknown as Date),
      name: '',
      vendor: '',
      cardName: '',
    };
  }
}
